import { Component, OnInit } from "@angular/core";
import { Users } from "../models/user";
import { QuizAppService } from "../services/quizapp.service";
// import * as Swal from "sweetalert2";
import Swal from "sweetalert2";

@Component({
  selector: "app-documentation",
  templateUrl: "./home.component.html",
  styleUrls: ["./home.component.css"]
})
export class HomeComponent implements OnInit {
  userArray: Users[]; //first create, use in the fetchBooks
  user = new Users();

  constructor(private userService: QuizAppService) {}
  ngOnInit(): void {
    this.fetchUser();
  }
  fetchUser(): void {
    this.userService.getUsers().subscribe(
      userArray => (this.userArray = userArray),
      error => (this.userArray = <any>error)
    );
  }
  add(): void {
    this.userService.addUsers(this.user).subscribe(
      () => {
        this.user.firstname = null;
        this.user.lastname = null;
        this.user.password = null;
        this.fetchUser();
        Swal.fire("Successfully Login!", "You clicked the button!", "success");
      },
      error => (this.userArray = <any>error)
    );
  }
}
