import { Injectable } from "@angular/core";
import { VERSION } from "@angular/core";
import { Http, Response, Headers, RequestOptions } from "@angular/http";
import { Observable } from "rxjs"; //angular 6, before import { Observable } from 'rxjs/Observable';
import { Users } from "../models/user";
import { catchError, map, tap } from "rxjs/operators"; //angular 6, before import 'rxjs/add/operator/map';
import { HttpClient, HttpHeaders } from "@angular/common/http";

const httpOptions = {
  HEADERS: new HttpHeaders({ "Content-Type": "application/json" })
};

@Injectable({ providedIn: "root" }) //angular 6
export class QuizAppService {
  API_KEY = "YOUR_API_KEY";
  private heroesUrl = "api/heroes";
  userUrl =
    "https://quizapp-dfdb.restdb.io/rest/profile?apikey=5e003489b68f0802dd3e5c67";

  constructor(private http: Http) {}

  getUsers(): Observable<Users[]> {
    return this.http.get(this.userUrl).pipe(
      map((response: any) => response.json()),
      catchError((error: any) =>
        Observable.throw(error.json().error || "Server error")
      )
    );
  }
  addUsers(user: Users): Observable<Users> {
    const myObj = {
      firstname: user.firstname,
      lastname: user.lastname,
      password: user.password
    };
    let headers = new Headers({ "Content-Type": "application/json" });
    let options = new RequestOptions({ headers: headers });
    return this.http.post(this.userUrl, myObj, options).pipe(
      map(this.extractData),
      catchError((error: any) =>
        Observable.throw(error.json().error || "Server error")
      )
    );
  }

  extractData(res: Response) {
    let body = res.json();
    return body || {};
  }
}
